@import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';
@import '../../node_modules/animate.css/animate.min.css';
@import url('https://fonts.googleapis.com/css?family=Nunito:300,400,500');

body {
  font-family: 'Nunito' !important;
}

.modal-preview {
  width : 90% !important;

}

.bg-half {
  position: fixed;
  background: linear-gradient(to bottom,  rgb(241, 41, 91),rgb(161, 11, 44)), url(tile.png);
  background-size: 2.5%;
  background-blend-mode: multiply;
  top : 0;
  left : 0;
  width : 100%;
  height: 50vh;
}

.bg-loading {
  background: linear-gradient(to bottom,  rgb(241, 41, 91),rgb(161, 11, 44)), url(tile.png);
  background-size: 2.5%;
  background-blend-mode: multiply;
  width: 100%;
}

.wrapper {
  position: fixed;
  top : 50%;
  left : 0;
  right : 0;
  margin : 0;
  width: 100%;
  height : 100%;
  transform: translateY(-50%);
  overflow-y : auto;
  // display: flex;
  // flex-direction: column;
  // justify-content: center;
  // align-items: center;
}

.hour-options {
  background : linear-gradient(to top, #1dd1a1, #0abde3), url(tile.png);
  background-size: 15%;
  background-blend-mode: multiply;
  border-width: 0;
  color : white;
  transition: 500ms ease-in-out;

  & h4::selection {
    background : transparent;
  }

  & h4:hover {
    cursor: pointer;
  }
  
  &:hover {
    // background : linear-gradient(to bottom, #1dd1a1, #0abde3), url(tile.png);
    // background-size: 15%;
    // background-blend-mode: multiply;
    box-shadow: 0px 3px 17px -5px rgba(0,0,0,0.87);
    transition: 500ms ease-in-out;
  }
  
  &.active-time {
    background : linear-gradient(to top, #222f3e, #576574);
    border : 0.5px solid #576574;
    box-shadow: 0px 3px 17px -5px rgba(0,0,0,0.5);
  }

  .front-office {
    position: absolute;
    top : -5px;
    right : -5px;
    background : #ee5253;
    border-radius: 25%;
    padding : 1px 8px;
    color : white;
  }
}

#dash-wrapper {
  overflow-y: hidden;
}

.login-btn {
  padding: 15px;
  display: inline-block;
  background: white;
  border-radius: 0.25em;
  position: absolute;
  top : 15px;
  right : 15px;
  z-index: 40;
}

.btn-group {
  button:not(:first-child):not(:nth-last-child()) {
    border-left: 1px solid #999 !important;
    border-right: 1px solid #999 !important;
  }
}

.logout-btn {
  position: absolute;
  bottom : 0;
  left : 0;
  width: 100%;
  border-top : 0.25px solid #bbb;
  padding : 5px 10px;
}

.overflow-scroll {
  height: 100%;
  overflow-y : auto;
}

.mdo-logo {
  width: 30%;
}

.loading-container {
  position: fixed;
  background : rgba(0, 0, 0, 0.8);
  top : 0;
  left : 0;
  width : 100%;
  height : 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  z-index: 100;
}

.calendar-toggle {
  cursor: pointer;
  background: transparent;
  // &:hover {
  //   background : rgba(0, 0, 0, 0.25);
  //   transition : 500ms ease-in-out;
  // }
  // transition : 500ms ease-in-out;
}

.setting-modal {
  width: 98% !important;
  max-width: 98% !important;
}

.table-light {
  cursor: pointer;
  &:hover {
    background : rgba(0, 0, 0, 0.09);
    transition : 500ms ease-in-out;
  }
  transition : 500ms ease-in-out;
}

.table-header {
  cursor: pointer;
  text-align: center;
}

@media (min-width : 300px) and (max-width : 768px) {
  .mdo-logo {
    width: 75%;
  }
}